import React, { useEffect, useState } from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Checkbox from '@mui/material/Checkbox';
import InputLabel from '@mui/material/InputLabel';
import TextareaAutosize from '@mui/material/TextareaAutosize';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import Link from '@components/link';

const Contact = () => {
  const clientnameId = 'contact__formClientname';
  const emailId = 'contact__formEmail';
  const email2Id = 'contact__formEmail2';
  const addressId = 'contact__formAddress';
  const inquiryId = 'contact__formInquiry';

  const [forms, setForms] = useState({
    [clientnameId]: '',
    [emailId]: '',
    [email2Id]: '',
    [addressId]: '',
    [inquiryId]: ''
  });

  const [isInvalids, setIsInvalids] = useState({
    [emailId]: false,
    [email2Id]: false
  });

  const [disabledButton, setDisabledButton] = useState(true);

  const checkForm = () => {
    let isInvalidEmailId = false;
    let isInvalidEmail2Id = false;

    if (
      forms[emailId] &&
      forms[email2Id] &&
      forms[emailId] !== forms[email2Id]
    ) {
      isInvalidEmailId = true;
      isInvalidEmail2Id = true;
    }

    // フォームボタンが押せなくなる条件を記述
    setDisabledButton(
      !forms[clientnameId] ||
        !forms[emailId] ||
        !forms[email2Id] ||
        !forms[inquiryId] ||
        isInvalidEmailId ||
        isInvalidEmail2Id
    );

    setIsInvalids({
      ...isInvalids,
      [emailId]: isInvalidEmailId,
      [email2Id]: isInvalidEmail2Id
    });
  };

  useEffect(() => {
    checkForm();
  }, [forms]);

  const onChangeForm = (key, value) => {
    setForms({
      ...forms,
      [key]: value
    });
  };

  return (
    <form
      action='https://docs.google.com/forms/u/2/d/e/1FAIpQLSezATzl5zb4rVgOMdghXC1LhyhzXYIVaytaFpF0Qh4d_fi1_Q/formResponse'
      method='POST'
    >
      {[
        {
          name: 'entry.1650788426',
          id: clientnameId,
          type: 'text',
          required: true,
          label: 'お名前',
          placeholder: '達磨 大志'
        },
        {
          id: emailId,
          type: 'email',
          required: true,
          label: 'メールアドレス',
          placeholder: 'taishi@daruma-inv.com'
        },
        {
          name: 'entry.1696363940',
          id: email2Id,
          type: 'email',
          required: true,
          label: 'メールアドレス（確認用）',
          placeholder: 'taishi@daruma-inv.com',
          invalidMessage: 'メールアドレスが一致していません。',
          invalidPaste: true
        },
        {
          name: 'entry.1411341641',
          id: addressId,
          type: 'text',
          label: 'ご住所',
          placeholder: '東京都渋谷区千駄ヶ谷2-10-5'
        },
        {
          name: 'entry.881256958',
          id: inquiryId,
          required: true,
          label: 'お問い合わせ内容',
          placeholder:
            '不動産の購入・売却、などなどお気軽にお問い合わせください。',
          textarea: true,
          rows: 3
        }
      ].map((d, i) => {
        const name = d.name || '';
        const placeholder = d.placeholder || '';

        return (
          <Box key={i} mb={8}>
            <InputLabel
              htmlFor={d.id}
              sx={{
                mb: 2
              }}
            >
              <Typography
                component='span'
                fontSize={10}
                fontWeight={700}
                lineHeight='28px'
                px={0.5}
                mr={1}
                color={d.required ? '#fff' : '#3c3c3c'}
                bgcolor={d.required ? '#3c3c3c' : '#ddd'}
              >
                {d.required ? '必須' : '任意'}
              </Typography>
              {d.label}
            </InputLabel>
            {d.textarea ? (
              <TextareaAutosize
                name={name}
                id={d.id}
                required={d.required}
                placeholder={placeholder}
                value={forms[d.id]}
                rows={3}
                style={{
                  width: '100%',
                  height: '140px',
                  backgroundColor: '#f4f4f4',
                  color: '#151618',
                  fontSize: '14px',
                  fontFamily: [
                    '"Noto Serif JP"',
                    '"ヒラギノ明朝 Pro W3"',
                    '"Hiragino Mincho Pro"',
                    '"ＭＳ Ｐ明朝"',
                    '"MS PMincho"',
                    'serif'
                  ].join(',')
                }}
                onChange={e => onChangeForm(d.id, e.target.value)}
              />
            ) : (
              <TextField
                name={name}
                id={d.id}
                variant='standard'
                fullWidth
                required={d.required}
                type={d.type}
                placeholder={placeholder}
                value={forms[d.id]}
                error={isInvalids[d.id]}
                helperText={isInvalids[d.id] && d.invalidMessage}
                onChange={e => onChangeForm(d.id, e.target.value)}
                onPaste={e => {
                  if (d.invalidPaste) {
                    e.preventDefault();
                    return false;
                  }
                }}
              />
            )}
          </Box>
        );
      })}
      <Typography my={3}>
        <Checkbox required />
        <Link href='/privacy/' target='_blank'>
          プライバシーポリシー
        </Link>{' '}
        に同意します。
      </Typography>
      <Button
        type='submit'
        fullWidth
        sx={{
          bgcolor: '#d6d6d6',
          height: 80
        }}
      >
        <Typography color='#3c3c3c'>上記の内容で送信する</Typography>
      </Button>
      <Typography mt={2} fontSize={14} color='#707070'>
        ※上のフォームから送信がうまくいかない場合は{' '}
        <Link
          href='https://docs.google.com/forms/d/e/1FAIpQLSezATzl5zb4rVgOMdghXC1LhyhzXYIVaytaFpF0Qh4d_fi1_Q/viewform'
          target='_blank'
        >
          こちら
          <i className='fas fa-external-link-alt'></i>
        </Link>{' '}
        からお問い合わせください。
      </Typography>
    </form>
  );
};

export default Contact;
