import React from 'react';
import { navigate } from 'gatsby';
import MuiLink from '@mui/material/Link';

const Link = props => {
  const children = props.children || props.href;

  // target="_blank" がついていれば Ex/In 問わず a タグ (MuiLink) 利用確定
  if (props.target === '_blank') {
    return (
      <MuiLink {...props} rel='noopener noreferrer'>
        {children}
      </MuiLink>
    );
  }

  // それ以外で外部リンクの場合はシンプルな MuiLink
  if (/^(http:|https:)?\/\/.+$/.test(props.href)) {
    return <MuiLink {...props}>{children}</MuiLink>;
  }

  // 内部リンクの場合は MuiLink + navigate 利用
  return (
    <MuiLink
      {...props}
      onClick={e => {
        e.preventDefault();
        navigate(props.href);
      }}
    >
      {children}
    </MuiLink>
  );
};

export default Link;
